.background-gif{
  background-image: url('../../images/BaseballField.jpg');
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;

}

@-webkit-keyframes gm-slidein {
    from {
        -webkit-transform:translate3d(0,-100%,0);
        opacity:0;
        transform:translate3d(0,-100%,0);
    }

    to {
        -webkit-transform:none;
        opacity:1;
        transform:none;
    }
}

@keyframes gm-slidein {
    from {
        -webkit-transform:translate3d(0,-100%,0);
        opacity:0;
        transform:translate3d(0,-100%,0);
    }

    to {
        -webkit-transform:none;
        opacity:1;
        transform:none;
    }
}

.login-button{
  -webkit-animation:gm-slidein 5s 1;
  animation:gm-slidein 5s 1;
  margin:0 auto;
  max-width:50%;
  padding:4em 0 2em;
  text-align:center;
  color: white;
  font-size: 25px;
  font-family: 'Exo', sans-serif;
}

.button{
  border-radius: 5px;
  font-size: 14px;
  height: 40px;
  width: 110px;
  text-align: center;
  color: #222;
  background: #F2F2F2;
  border-style: none;
}



